import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

const calcVals = [
  {value: 0,
   id: "zero"},
  {value: 1,
   id: "one"},
  {value: 2,
   id: "two"},
  {value: "+",
  id: "add"},
  {value: 3,
   id: "three"},
  {value: 4,
   id: "four"},
  {value: 5,
   id: "five"},
  {value: "-",
   id: "subtract"},
  {value: 6,
   id: "six"},
  {value: 7,
   id: "seven"},
  {value: 8,
   id: "eight"},
  {value: "*",
   id: "multiply"},
  {value: 9,
   id: "nine"},
  {value: ".",
   id: "decimal"},
  {value: "=",
   id: "equals"},
  {value: "/",
   id: "divide"}
  ];

function Power(props) {
  return (
    <div>
      <button id="power" class="btn extra" onClick={props.handleOnOff}>ON</button>
    </div>
  );
}

function Clear(props) {
  return (
    <div>
      <button id="clear" class="btn extra" onClick={props.handleClear} disabled={props.disabled}>AC</button>
    </div>
  )
}

function Display(props) {
  return <input type="text" id="display" name="display" value={props.value}/>
}

function CalcButton(props) {
  return (
    <div>
      <button id={props.id} class="btn" value={props.value} onClick={() => props.handleClick(props.id)} disabled={props.disabled}>{props.value}</button>
    </div>
  );
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      input: [],
      display: 0,
      buttonsDisabled: true
    }
    this.handleOnOff = this.handleOnOff.bind(this);
    this.validationChecks = this.validationChecks.bind(this);
    this.handleCalcButtonClick = this.handleCalcButtonClick.bind(this);
    this.handleClear = this.handleClear.bind(this);
  }
  validationChecks() {
    //check for multiple leading zeroes and if decimal was entered more than once in a number
    let opsRE = /([+\-*\/])/g;
    let splitVals = this.state.display.toString().split(opsRE);
    let removeZeroesRE = /^0+(\d+)/g;
    let decRE = /(?<=\.\d?)\./g;
    let filteredVals = [];

    for (let i = 0; i < splitVals.length; i++) {
      if (splitVals[i]) {
        filteredVals.push(splitVals[i].replace(removeZeroesRE, "$1").replace(decRE, ""));
      }
    }
    this.setState({
      input: filteredVals,
      display: filteredVals.join("")
    });
  }
  componentDidUpdate(prevProps, prevState) {
    if(prevState.input.length != this.state.input.length) {
      this.validationChecks();
    }
  }
  handleCalcButtonClick(el) {
    let valueClicked = document.getElementById(el).value;
    let operators = ["+", "-", "*", "/"];

    //check if equals button was clicked
    if(valueClicked === "=") {
      this.setState( prevState => ({
        input: eval(prevState.display),
        display: eval(prevState.display)
      }));

      //check for multiple consecutive operators
    } else if (operators.includes(valueClicked) && operators.includes(this.state.input[this.state.input.length - 1])) {
        let removedEl = this.state.input.splice(this.state.input.length - 1, 1);
        this.setState( prevState => ({
          input: prevState.input.concat(valueClicked),
          display: prevState.input.concat(valueClicked).join("")
        }));

    } else {
        this.setState(prevState => ({
          input: prevState.input.concat(valueClicked),
          display: prevState.input.concat(valueClicked).join("")
        }));
    }
  }
  handleOnOff() {
    this.setState({
      buttonsDisabled: !this.state.buttonsDisabled
    });
    this.handleClear();
  }
  handleClear() {
    this.setState({
      input: [],
      display: 0
    });
  }
  render() {
    const calcComponents = calcVals.map(item => <CalcButton id={item.id} key={item.id} value={item.value} handleClick={this.handleCalcButtonClick} disabled={this.state.buttonsDisabled}/>);

   return (
     <div class="container">
       <Display value={this.state.display}/>
       <div class="calculator">
         <Power handleOnOff={this.handleOnOff}/>
         <Clear handleClear={this.handleClear} disabled={this.state.buttonsDisabled}/>
         <br/>
         {calcComponents}
       </div>
     </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
